$x-light: #ffffff;

.sea {
    position: relative;
    overflow: hidden;

    .wave {
        position: absolute;
        height: 3px;
        background-color: $x-light;
        border-radius: 100%;
        opacity: 0.2;
        animation: wave 4s linear infinite;
    }

    @for $i from 1 through (36) {
        $size: random(170) + 50px;

        .wave:nth-of-type(#{$i}) {
            width: $size;
            bottom: calc(#{random(100) + vh} - 300px);
            left: random(100) + vw;
            opacity: random(5) * 0.1;
            animation-delay: -(random(3)) + s;
        }
    }

    @for $i from 1 through (6) {
        .seal:nth-of-type(#{$i}) {
            animation-delay: -(random(3)) + s;
        } 
    }

}

@keyframes wave {

    0%,
    100% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-80px);
    }
}